/* src/components/Home.css */
.home-container {
    background-color: #3498db; /* Light Blue */
    color: #ecf0f1; /* Light Gray */
    text-align: center;
    padding: 50px;
  }
  
  header h1 {
    font-size: 3.5em;
    color: #fff; /* White */
  }
  
  .sub-heading {
    font-size: 1.8em;
    margin-top: 10px;
    font-style: italic;
  }
  
  .services {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    margin: 40px 0;
  }
  
  .service-card {
    background-color: #2c3e50; /* Dark Blue-Gray */
    color: #ecf0f1;
    padding: 20px;
    border-radius: 10px;
    margin: 15px;
    text-align: left;
    max-width: 250px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    transition: transform 0.3s ease-in-out;
  }
  
  .service-card:hover {
    transform: scale(1.05);
  }
  
  .service-card img {
    max-width: 50px;
    margin-bottom: 10px;
  }
  
  .service-card h3 {
    font-size: 1.8em;
    margin-bottom: 10px;
    color: #3498db; /* Light Blue */
  }
  
  .service-card p {
    font-size: 1.2em;
  }
  
  .more-content {
    margin: 40px 0;
  }
  
  .more-content h2 {
    font-size: 2.5em;
    color: #fff; /* White */
  }
  
  .more-content p {
    font-size: 1.2em;
    line-height: 1.5;
  }
  
  /* Add more styles as needed */
  