.navigation {
  background-color: #333;
  padding: 10px;
  position: relative;
  transition: background-color 0.3s;
}

.navigation.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.navigation ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
}

.navigation li {
  display: inline;
  margin-right: 20px;
}

.navigation a {
  text-decoration: none;
  color: white;
}
